.wp-block[data-type="tasty-block/container"] > .block-editor-block-list__block-edit {
  padding: 30px 50px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06); }

.block-editor-url-input.url-input input[type="text"] {
  width: 100%; }

.components-button.image-button {
  height: auto;
  padding: 0; }

.splash-image .components-button.button-large {
  margin: 50px auto 150px; }

.splash-image .components-button.image-button {
  width: 100%; }

.editor-styles-wrapper .wp-block-tastydigital-splash-box h2 {
  margin: 5px 0 0;
  color: #FFF;
  font-weight: 700; }

.wp-block[data-type="core/group"] .block-editor-inner-blocks > .block-editor-block-list__layout {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center; }
  .wp-block[data-type="core/group"] .block-editor-inner-blocks > .block-editor-block-list__layout > * {
    padding-left: 0.6em;
    padding-right: 0.6em; }

.wp-block[data-type="core/heading"] .is-style-mega-title {
  text-transform: uppercase;
  font-weight: 800; }

.wp-block[data-type="core/heading"] h1.is-style-mega-title {
  font-size: 144px;
  letter-spacing: -6px; }

.wp-block[data-type="core/heading"] h2.is-style-mega-title {
  font-size: 104px;
  letter-spacing: -5px; }

.wp-block[data-type="core/heading"] h3.is-style-mega-title {
  font-size: 64px;
  letter-spacing: -4px; }

.wp-block[data-type="core/heading"] h4.is-style-mega-title {
  font-size: 44px;
  letter-spacing: -3px; }

.wp-block[data-type="core/heading"] h5.is-style-mega-title {
  font-size: 24px;
  letter-spacing: -1px; }

.wp-block[data-type="core/heading"] h6.is-style-mega-title {
  font-size: 14px; }
